    //             let hasExistsCity = cityRecordList.filter(oriCity => oriCity.value == cityID) //
    //             if(hasExistsCity.length > 0) {
    //                 let countyRecordList = hasExistsCity[0].children;
    //                 let hasExistsCounty = countyRecordList.filter(oriCounty => oriCounty.value == countyID) //
    //                 if(hasExistsCounty.length < 1) {
    //                     hasExistsCity[0].children.push({value: countyID,label:item.countyName})
    //                 }
    //             } else {
    //                 hasExistsProvince[0].children.push({
    //                     value: cityID,
    //                     label: item.cityName,
    //                     children: [
    //                         {
    //                             value: countyID,
    //                             label: item.countyName
    //                         }
    //                     ]
    //                 })
    //             }
    //         } else {
    //             //
    //             regionList.push({
    //                 value: provinceID,
    //                 label: item.provinceName,
    //                 children: [
    //                     {
    //                         value: cityID,
    //                         label: item.cityName,
    //                         children: [
    //                             {
    //                                 value: countyID,
    //                                 label: item.countyName
